.shape-container{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    min-width: 20vw;
    height: 20vh;
    background-color: rgba(28, 221, 209, .8);
    margin: .5em;
    border-radius: 10px;
}

.shape-container button{
    cursor: pointer;
    margin-top: 1em;
    border: none;
    padding: .5em 1em;
    border-radius: 5px;
}
