main > section {
    margin-top: 80px;
    width: 100%;
    min-height: calc(100vh - 160px);
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}

.user-shape{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
.user-shape > div{
    width: 30%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 2em;
}
.user-shape > div > button{
    width: 15%;
    padding: .8em;
}
.user-shape select{
    
    width: 70%;
    padding: .8em;
}
.user-shape > section{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}





.signin-form, .signup-form{
    text-align: center;
    width: 100%;
}
form{
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
form > div, div.signin-submit{
    width: 30%;
}















.shape-section, .shape-result, .calculate-shape{
    width: 30%;
    min-height: 100%;
}
.shape-section h2, .shape-section > p{
    margin-bottom: 1em;
}
.calculate-shape > div > div{
    width: 100%;
}

.shape-result{
    border: 2px solid #1CDDD1;
    padding: 3em 1em;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.shape-result dt, .shape-result dd{
    margin-bottom: 1em;
}
