.requirement-container button{
    width: 80%;
    min-height: 80px;
    border: none;
    background-color:#1CDDD1;
    cursor: pointer;
    border-radius: 10px;
    margin: 1em 0;
}
.requirement-container button:disabled{
    background-color: rgba(28, 221, 208, 0.568);
}
.requirement-container ul{
    list-style-type:none;
}
.requirement-container li{
    color: #fff;
}
