header{
    position: fixed;
    top: 0;
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 1em;
    border-bottom: 1px solid rgba(219, 219, 219, 1);
    background-color: #fff;
    /*box-shadow: 0 2px 5px 0 gray;*/
}
header > div, header > a {
    display: flex;
    flex-direction: row;
    align-items: center;
}
header > div > div {
    margin-left: 2em;
}
header > div > div > a{
    margin-left: 2em;
    padding: .7em 1.5em;
    border-radius: 10px;
}
header ul{
    list-style-type: none;
    display: inline;
}
header li{
    display: inline;
    margin: 0 1.5em;
    line-height: 70px;
}
header a{
    text-decoration: none;
    padding: .5em 0;
}
header ul >li >a:hover, .active{
    color: #1CDDD1;
    border-bottom: 2px solid #1CDDD1;
}

.submit{
    background-color: #1CDDD1;
    color: #fff;
}
.submit:hover{
    background-color: rgb(14, 185, 174);
}
.submit-alt{
    background-color: rgb(215, 236, 235);
}
.submit-alt:hover{
    background-color: rgb(188, 206, 205);
}
.reset{
    background-color: rgb(233, 72, 72);
    color: #fff;
}
.reset:hover{
    background-color: rgb(202, 53, 53);
}